import React, { useState, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import { Image, X, Send, Save, Globe, Lock } from 'lucide-react';
import { useAuthStore } from '../stores/authStore';
import { postsApi } from '../api/posts';
import toast from 'react-hot-toast';

interface PostForm {
  title: string;
  content: string;
  visibility: 'public' | 'private';
}

const CreatePost: React.FC = () => {
  const navigate = useNavigate();
  const { user } = useAuthStore();
  const fileInputRef = useRef<HTMLInputElement>(null);
  
  const [images, setImages] = useState<File[]>([]);
  const [imagePreviews, setImagePreviews] = useState<string[]>([]);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [isDraft, setIsDraft] = useState(false);

  const {
    register,
    handleSubmit,
    watch,
    // setValue,
    formState: { errors },
  } = useForm<PostForm>({
    defaultValues: {
      visibility: 'public',
    },
  });

  const watchedVisibility = watch('visibility');
  const watchedContent = watch('content');

  const handleImageSelect = (event: React.ChangeEvent<HTMLInputElement>) => {
    const files = Array.from(event.target.files || []);
    console.log(':', files.length);
    console.log(':', images.length);
    
    if (files.length + images.length > 9) {
      toast.error('9');
      return;
    }

    const newImages = [...images, ...files];
    setImages(newImages);
    console.log(':', newImages.length);

    // 
    files.forEach((file, index) => {
      const reader = new FileReader();
      reader.onload = (e) => {
        const result = e.target?.result as string;
        console.log(` ${index + 1} :`, result ? '' : '');
        setImagePreviews(prev => {
          const newPreviews = [...prev, result];
          console.log(':', newPreviews.length);
          return newPreviews;
        });
      };
      reader.readAsDataURL(file);
    });
  };

  const removeImage = (index: number) => {
    const newImages = images.filter((_, i) => i !== index);
    const newPreviews = imagePreviews.filter((_, i) => i !== index);
    setImages(newImages);
    setImagePreviews(newPreviews);
  };

  const onSubmit = async (data: PostForm, saveAsDraft = false) => {
    if (!user) {
      toast.error('');
      return;
    }

    console.log(':', user);
    console.log(':', data);
    console.log(':', images.length);

    setIsSubmitting(true);
    setIsDraft(saveAsDraft);

    try {
      // API
      const postData = {
        title: data.title,
        content: data.content,
        post_type: 'text' as const,
        images: images,
      };

      console.log(':', postData);
      const newPost = await postsApi.createPost(postData);
      console.log(':', newPost);
      
      if (saveAsDraft) {
        toast.success('');
      } else {
        toast.success('');
        navigate('/');
      }
    } catch (error: any) {
      console.error(':', error);
      console.error(':', error.response?.data);
      console.error(':', error);
      if (error.response?.data?.images) {
        console.error(':', error.response.data.images);
      }
      if (error.response?.data?.uploaded_images) {
        console.error(':', error.response.data.uploaded_images);
      }
      toast.error(saveAsDraft ? '' : '');
    } finally {
      setIsSubmitting(false);
      setIsDraft(false);
    }
  };

  const handleSaveDraft = () => {
    handleSubmit((data) => onSubmit(data, true))();
  };

  const getVisibilityIcon = () => {
    switch (watchedVisibility) {
      case 'public':
        return <Globe className="w-4 h-4" />;
      case 'private':
        return <Lock className="w-4 h-4" />;
      default:
        return <Globe className="w-4 h-4" />;
    }
  };

  const getVisibilityText = () => {
    switch (watchedVisibility) {
      case 'public':
        return '';
      case 'private':
        return '';
      default:
        return '';
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-4xl mx-auto py-8 px-4">
        <div className="bg-white rounded-lg shadow-sm">
          {/*  */}
          <div className="flex items-center justify-between p-6 border-b">
            <h1 className="text-xl font-semibold text-gray-900"></h1>
            <div className="flex items-center space-x-3">
              <button
                type="button"
                onClick={handleSaveDraft}
                disabled={isSubmitting}
                className="flex items-center px-4 py-2 text-sm font-medium text-gray-700 bg-gray-100 rounded-md hover:bg-gray-200 disabled:opacity-50"
              >
                <Save className="w-4 h-4 mr-2" />
                {isDraft ? '...' : ''}
              </button>
              <button
                type="submit"
                form="post-form"
                disabled={isSubmitting}
                className="flex items-center px-4 py-2 text-sm font-medium text-white bg-primary-600 rounded-md hover:bg-primary-700 disabled:opacity-50"
              >
                <Send className="w-4 h-4 mr-2" />
                {isSubmitting && !isDraft ? '...' : ''}
              </button>
            </div>
          </div>

          <form id="post-form" onSubmit={handleSubmit((data) => onSubmit(data, false))}>
            <div className="p-6 space-y-6">
              {/*  */}
              <div>
                <label htmlFor="title" className="block text-sm font-medium text-gray-700 mb-2">
                  
                </label>
                <input
                  {...register('title', { required: '' })}
                  type="text"
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                  placeholder="..."
                />
                {errors.title && (
                  <p className="mt-1 text-sm text-red-600">{errors.title.message}</p>
                )}
              </div>

              {/*  */}
              <div>
                <label htmlFor="content" className="block text-sm font-medium text-gray-700 mb-2">
                  
                </label>
                <textarea
                  {...register('content', { required: '' })}
                  rows={8}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent resize-none"
                  placeholder="..."
                />
                {errors.content && (
                  <p className="mt-1 text-sm text-red-600">{errors.content.message}</p>
                )}
                <div className="mt-2 text-right text-sm text-gray-500">
                  {watchedContent?.length || 0} / 2000
                </div>
              </div>

              {/*  */}
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                   (9) - : {imagePreviews.length} 
                </label>
                <div className="grid grid-cols-3 gap-4">
                  {imagePreviews.map((preview, index) => {
                    console.log(` ${index + 1}:`, preview ? '' : '');
                    return (
                      <div key={index} className="relative">
                        <img
                          src={preview}
                          alt={` ${index + 1}`}
                          className="w-full h-32 object-cover rounded-lg"
                          onError={(e) => {
                            console.error(` ${index + 1} :`, e);
                          }}
                        />
                        <button
                          type="button"
                          onClick={() => removeImage(index)}
                          className="absolute -top-2 -right-2 w-6 h-6 bg-red-500 text-white rounded-full flex items-center justify-center hover:bg-red-600"
                        >
                          <X className="w-4 h-4" />
                        </button>
                      </div>
                    );
                  })}
                  {images.length < 9 && (
                    <button
                      type="button"
                      onClick={() => fileInputRef.current?.click()}
                      className="w-full h-32 border-2 border-dashed border-gray-300 rounded-lg flex flex-col items-center justify-center hover:border-primary-500 hover:bg-primary-50"
                    >
                      <Image className="w-8 h-8 text-gray-400 mb-2" />
                      <span className="text-sm text-gray-500"></span>
                    </button>
                  )}
                </div>
                <input
                  ref={fileInputRef}
                  type="file"
                  accept="image/*"
                  multiple
                  onChange={handleImageSelect}
                  className="hidden"
                />
              </div>

              {/*  */}
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                {/*  */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    
                  </label>
                  <select
                    {...register('visibility')}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500"
                  >
                    <option value="public"></option>
                    <option value="private"></option>
                  </select>
                  <div className="mt-2 flex items-center text-sm text-gray-500">
                    {getVisibilityIcon()}
                    <span className="ml-1">{getVisibilityText()}</span>
                  </div>
                </div>

              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default CreatePost;
